
import { ModalProvider } from 'styled-react-modal'
import RouterView from './routers'
import { GlobalStyles } from '@material-ui/core';
import { css, Theme, useTheme } from '@emotion/react';
import { globalCss } from '@loopring-web/component-lib/static-resource';
import { GlobalProvider, Web3ReactManager } from './provider/';

const App = () => {
    const theme: Theme = useTheme();
    return <>
      <GlobalStyles styles={css` 
          ${globalCss};
          body{
              ${theme.mode === 'dark' ? `
                color: ${theme.colorBase.textPrimary};
              `:``}
          }
          body:before {
            ${theme.mode === 'dark' ? `
                background: #191C30;
                background: ${theme.colorBase.background().bg};
           ` : ''}
          }
    }`}></GlobalStyles>
      <ModalProvider>
          <GlobalProvider>
              <Web3ReactManager>
                  <RouterView />
              </Web3ReactManager>
          </GlobalProvider>
      </ModalProvider>
  </>
}

export default App
