import { createSlice, PayloadAction } from '@reduxjs/toolkit'
// import { ColorLib, LangType } from 'defs/common_defs'

import {LanguageType, PlatFormType, ThemeType, ThemeProps,LanguageProps, PlatFormProps} from "./interface";

export interface SettingsState {
  themeMode: ThemeProps
  language: LanguageProps
  platform: PlatFormProps
}

// const init_mode = ColorLib.dark
// const init_lang = LangType.en

const initialState:SettingsState = {
  themeMode: ThemeType.dark,
  language:  LanguageType.en_US,
  platform:   PlatFormType.Desktop
}

const settingsSlice = createSlice({
  name: 'settings',
  initialState,
  reducers: {
    setTheme(state, action: PayloadAction<ThemeProps>) {
      state.themeMode = action.payload as any
    },
    setLanguage(state, action: PayloadAction<keyof typeof LanguageType>) {
      state.language = action.payload
    },
    setPlatform(state, action: PayloadAction<keyof typeof PlatFormType>) {
      state.platform = action.payload
    },
  },
})
export const { setTheme,setPlatform,setLanguage } = settingsSlice.actions
// export const { setTheme,setPlatform,setLanguage } = settingsSlice.actions
export default settingsSlice
