import { DepositInfoProps, ResetInfoProps, TransferInfoProps, WithdrawInfoProps } from '../../components';
import { IBData } from 'static-resource';

export enum ModalType {
    transfer = 'transfer',
    deposit = 'deposit',
    withdraw = 'withdraw',
}

export type ModalTypeKeys = keyof typeof ModalType

export type ModalStatePlayLoad = {
    isShow: boolean,
}

export interface ModalState {
    isShowTransfer: ModalStatePlayLoad & { props?: TransferInfoProps<IBData<any>, any> },
    isShowWithdraw: ModalStatePlayLoad & { props?: WithdrawInfoProps<IBData<any>, any> },
    isShowDeposit: ModalStatePlayLoad & { props?: DepositInfoProps<IBData<any>, any> },
    isShowResetAccount: ModalStatePlayLoad & { props?: ResetInfoProps<IBData<any>, any> },
}
