import { WithTranslation } from 'react-i18next';
import { FloatTag, IBData, TradeCalcData } from 'static-resource';
import { Box, Grid } from '@material-ui/core';
import { Avatar, Typography } from '@material-ui/core/';
import styled from '@emotion/styled';
import React from 'react';
import { floatTag, SwapProps, TradeFloat, useImage, useSettings } from '../../index';

type StyledProps = {
    custom: any
}
const TradeTitleStyled = styled(Box)<StyledProps>`
  height: 72px;


  .MuiAvatar-root {
    width: var(--chart-title-icon-size);
    height: var(--chart-title-icon-size);
  }

  .icon-next {
    margin-left: -${({theme}) => theme.unit}px;
  }

  h3.MuiTypography-root {
    font-size: 1.6rem;
    margin-left: ${({theme}) => theme.unit}px;
    color: ${({theme}) => theme.colorBase.textSecondary};

    .MuiTypography-root {
      margin: 0 ${({theme}) => theme.unit / 4}px;
    }

    .sell-coin {
      color: ${({theme}) => theme.colorBase.textPrimary};
    }
  }

  ${({theme, custom}) => floatTag({theme, custom})};

  .float-chart {
    margin-left: ${({theme}) => theme.unit}px;

    .chart-change {
      color: ${({theme}) => theme.colorBase.textSecondary};
    }
  }
` as React.ElementType<StyledProps>;

export const TradeTitle = <T extends IBData<I>,
    I,
    TCD extends TradeCalcData<I>>({
                                      swapTradeData, tradeCalcData, t,
                                      tradeFloat = {
                                          percent: 0,
                                          chartUnit: '',
                                          priceYuan: 0,
                                          priceDollar: 0,
                                          floatTag: FloatTag.none
                                      }
                                  }:
                                      Pick<SwapProps<T, I, TCD>, 'swapTradeData'>
                                      & Pick<SwapProps<T, I, TCD>, 'tradeCalcData'>
                                      & WithTranslation & { tradeFloat: TradeFloat }) => {
    const {buy, sell} = swapTradeData;
    const buyData = tradeCalcData.buyCoinInfoMap[ buy.belong ];
    const sellData = tradeCalcData.sellCoinInfoMap[ sell.belong ];
    const sellIconHasLoaded = useImage(sellData?.icon ? sellData?.icon : '').hasLoaded;
    const buyIconHasLoaded = useImage(buyData?.icon ? buyData?.icon : '').hasLoaded;
    const tradeFloatType = tradeFloat?.priceDollar === 0 ? FloatTag.none : tradeFloat?.priceDollar < 0 ? FloatTag.decrease : FloatTag.increase;
    const {currency} = useSettings();
    return <TradeTitleStyled custom={{chg: currency}}>{buyData && sellData ?
        <Grid container width={520} height={72}>
            <Grid item xs={12} height={28}>
                <Box display={'flex'} flexDirection={'row'} justifyContent={'flex-start'} alignItems={'center'}>
                    {/*//TODO:*/}
                    {/* src={tradeCalcData.sellCoinInfoMap[ sell.belong ].icon} />*/}
                    {/*<Avatar alt={buyData?.simpleName}  className={'icon-next'}*/}
                    {/*   src="https://exchange.loopring.io/assets/images/ethereum/assets/0x9A0aBA393aac4dFbFf4333B06c407458002C6183/logo.png"/>*/}
                    <Avatar variant="square" alt={sellData?.simpleName}
                        // src={sellData?.icon}
                            src={sellIconHasLoaded ? sellData?.icon : 'static/images/icon-default.png'}/>
                    <Avatar variant="square" alt={buyData?.simpleName} className={'icon-next'}
                        // src={buyData?.icon}
                            src={buyIconHasLoaded ? buyData?.icon : 'static/images/icon-default.png'}/>
                    <Typography variant={'h3'} component={'h3'}>
                        <Typography component={'span'} title={'sell'} className={'sell-coin'}>
                            {sellData?.simpleName}
                        </Typography>
                        <Typography component={'i'}>/</Typography>
                        <Typography component={'span'} title={'buy'}>
                            {buyData.simpleName}
                        </Typography>
                    </Typography>
                </Box>
            </Grid>
            <Grid item xs={12} height={36} display={'flex'} flexDirection={'row'} justifyContent={'flex-start'}
                  alignItems={'center'} className={'float-group'}>
                {/*//TODO:*/}

                <Typography variant={'h2'}>   {tradeFloat.priceDollar} {buyData?.simpleName}    </Typography>
                <Box display={'flex'} flexDirection={'column'} alignItems={'flex-start'} justifyContent={'center'}
                     className={'float-chart'}>
                    <Typography variant={'body2'} component={'span'}
                                className={'chart-change'}>{t('chartChange', {chartUnit: tradeFloat.chartUnit})}</Typography>
                    <Typography variant={'h3'} component={'span'} className={`float-tag float-${tradeFloatType}`}>
                        {`${(tradeFloat.priceDollar > 0 ? '+' : '') + tradeFloat.priceDollar} (${tradeFloat.percent})`}</Typography>
                </Box>
            </Grid>
        </Grid> : <></>
    } </TradeTitleStyled>


    // <Avatar alt={coinMap[ belong ]?.simpleName}
    // src="https://exchange.loopring.io/assets/images/ethereum/assets/0x9A0aBA393aac4dFbFf4333B06c407458002C6183/logo.png"/>
    //     </Grid>
}