import React from 'react'
import { Box, Grid, MenuItem, Menu } from '@material-ui/core'
import styled from '@emotion/styled'
import { WithTranslation, withTranslation } from 'react-i18next'
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state'
import { cloneDeep } from 'lodash'
import { Button } from '../../basic-lib/btns'
import { Table, generateColumns, generateRows, Column } from '../../basic-lib/tables'
import { TablePagination } from '../../basic-lib'
import { Filter, TokenTypeCol } from './components/Filter'

interface Row {
    token: {
        type: TokenType;
        value: string;
    };
    amount: number;
    available: number;
    locked: number;
    filterColumn?: string
    tradePairList?: {
        first: string;
        last: string;
    }[]
    cellExpend?: {
        value: string
        children: []
        isExpanded: boolean
    }
    children?: Row[]
    isExpanded?: boolean
    format?: any
}

export enum TokenType {
    single = 'single',
    lp = 'lp'
}

export type TradePairItem = {
    first: string;
    last: string;
}

export type RawDataAssetsItem = {
    token: {
        type: TokenType,
        value: string
    }
    amount: number;
    available: number;
    locked: number;
    tradePairList?: TradePairItem[];
    smallBalance: boolean;
}

export interface AssetsTableProps {
    rawData: RawDataAssetsItem[];
    pagination?: {
        pageSize: number
    }
}

enum ActionType {
    filter = 'filter',
    page = 'page'
}

export const AssetsTable = withTranslation('tables')((props: WithTranslation & AssetsTableProps) => {
    const { t, pagination } = props
    const columnModeAssets: Column<Row, unknown>[] = [
        {
            key: 'token',
            name: t('labelToken'),
            formatter: ({row, column}) => {
                const token = row[column.key]
                const StyledToken = styled(Box)`
                    font-size: 13px;
                `
                return <StyledToken>{token.value}</StyledToken>
            }
        },
        {
            key: 'amount',
            name: t('labelAmount'),
        },
        {
            key: 'available',
            name: t('labelAvailable'),
        },
        {
            key: 'locked',
            name: t('labelLocked'),
        },
        {
            key: 'actions',
            name: t('labelActions'),
            minWidth: 400,
            formatter: ({ row }) => {
                const token = row['token']
                const isLp = token.type === TokenType.lp
                const tradePairs: TradePairItem[] = row['_rawData'][4] || []
                
                return (
                    <Grid container spacing={1} alignItems={'center'}>
                        <Grid item>
                            <Button variant={'outlined'} size={'medium'} color={'primary'}>{t('labelDeposit')}</Button>
                        </Grid>
                        <Grid item>
                            <Button variant={'outlined'} size={'medium'} color={'primary'}>{t('labelTransfer')}</Button>
                        </Grid>
                        <Grid item>
                            <Button variant={'outlined'} size={'medium'} color={'primary'}>{t('labelWithdraw')}</Button>
                        </Grid>
                        <Grid item>
                            {isLp
                                ? <Button variant={'outlined'} color={'primary'} disabled>{t('labelAMM')} </Button>
                                : <PopupState variant="popover" popupId="demo-popup-menu">
                                    {(popupState) => (
                                        <>
                                            <Button variant={'outlined'} size={'medium'} color="primary" {...bindTrigger(popupState)}>
                                                Trade
                                            </Button>
                                            <Menu {...bindMenu(popupState)}>
                                                {tradePairs.map(({first, last}) => {
                                                    const value = `${first}/${last}`;
                                                    return  (
                                                        <MenuItem onClick={popupState.close} key={value} value={value}>{value}</MenuItem>
                                                    )
                                                })}
                                            </Menu>
                                        </>
                                    )}
                                </PopupState>
                            }
                        </Grid>
                    </Grid>
                )
            }
        },
        {
            key: 'tradePairList',
            name: 'tradePairList',
            hidden: true
        }
    ]
    const formattedRawData = props.rawData.map(o => Object.values(o))
    const [filterTokenType, setFilterTokenType] = React.useState('All Tokens')
    const [hideSmallBalance, setHideSmallBalance] = React.useState(false)
    const [hideLPToken, setHideLPToken] = React.useState(false)
    const [totalData, setTotalData] = React.useState(formattedRawData)
    const [page, setPage] = React.useState(1)
    const pageSize = pagination ? pagination.pageSize : 10;

    const defaultArgs: any = {
        rawData: [],
        columnMode: columnModeAssets.filter(o => !o.hidden),
        generateRows,
        generateColumns,
    }

    const getRenderData = React.useCallback(() => pagination
            ? totalData.slice((page - 1) * pageSize, page * pageSize)
            : totalData
    , [page, pageSize, pagination, totalData])

    const updateData = React.useCallback(({ 
        actionType,
        currFilterTokenType = filterTokenType,
        currHideSmallBalance = hideSmallBalance,
        currHideLPToken = hideLPToken
    }) => {
        let resultData = cloneDeep(formattedRawData)
        // o[0]: token type
        if (currFilterTokenType !== 'All Tokens') {
            resultData = resultData.filter(o => 
                (o[0] as TokenTypeCol).value === currFilterTokenType
            )
        }
        if (currHideSmallBalance) {
            resultData = resultData.filter(o => !o[5])
        }
        if (currHideLPToken) {
            resultData = resultData.filter(o => (o[0] as TokenTypeCol).type === TokenType.single)
        }
        if (actionType === 'filter') {
            setPage(1)
        }
        setTotalData(resultData)
    }, [formattedRawData, filterTokenType, hideSmallBalance, hideLPToken])

    const handleFilterChange = React.useCallback(({ tokenType, currHideSmallBalance, currHideLPToken }) => {
        setFilterTokenType(tokenType)
        setHideSmallBalance(currHideSmallBalance)
        setHideLPToken(currHideLPToken)
        updateData({ actionType: ActionType.filter, currFilterTokenType: tokenType, currHideSmallBalance, currHideLPToken })
    }, [updateData])

    const handlePageChange = React.useCallback((page: number) => {
        setPage(page)
        updateData({ actionType: ActionType.page })
    }, [updateData])

    return <>
        <Filter originalData={formattedRawData} handleFilterChange={handleFilterChange} />
        <Table {...{...defaultArgs, ...props, rawData: getRenderData()}}/>
        {pagination && (
            <TablePagination page={page} pageSize={pageSize} total={totalData.length} onPageChange={handlePageChange} />
        )}
    </>
})
