import { HeaderRendererProps, SortableHeaderCell } from 'react-data-grid';
// import { DropDownIcon } from 'static-resource';
import React from 'react';
import styled from '@emotion/styled'
import { Box } from '@material-ui/core'

export interface LoopringTBHeaderProps<R> extends HeaderRendererProps<R> {
    child?: React.ElementType<any> | JSX.Element,
}

const StyledCellContentWrapper = styled(Box)`
    display: flex;
    align-items: center;
`

export function LoopringTBHeader<R>({
                                        sortDirection,
                                        sortColumn,
                                        onSort,
                                        column,
                                        onAllRowsSelectionChange,
                                        child,
                                        ...props
                                    }: LoopringTBHeaderProps<R>) {


    function getCell() {
        // console.log(child)
        // const _child = () => {
        //     debugger
        //     if(React.isValidElement(child)) {
        //         return React.cloneElement<any>(child, { column:column });
        //     } else{
        //         return child
        //     }
        // }
        if (column.sortable) {
            const showUp = sortColumn === column.key && sortDirection === 'ASC'
            const showDown = sortColumn === column.key && sortDirection === 'DESC'
            const StyledArrowSort = styled(Box)`
                margin-left: ${({theme}) => `${theme.unit / 2}px`};
                .up {
                    width: 0;
                    height: 0;
                    border: ${({theme}) => `${theme.unit / 2}px`} solid transparent;
                    border-bottom-color: ${showUp ? ({theme}) => `${theme.colorBase.textPrimary}` : ({theme}) => `${theme.colorBase.textSecondary}`};
                }
                .down {
                    width: 0;
                    height: 0;
                    border: ${({theme}) => `${theme.unit / 2}px`} solid transparent;
                    border-top-color: ${showDown ? ({theme}) => `${theme.colorBase.textPrimary}` : ({theme}) => `${theme.colorBase.textSecondary}`};
                    margin-top: ${({theme}) => `${theme.unit / 4}px`};
                }
            `
            return (
                <SortableHeaderCell
                    {...props}
                    column={column}
                    sortColumn={sortColumn}
                    onSort={onSort}
                    sortDirection={sortDirection}
                >
                    <StyledCellContentWrapper>
                        {child ? child : column.name}
                        <StyledArrowSort>
                            <div className="up"/>
                            <div className="down"/>
                        </StyledArrowSort>
                    </StyledCellContentWrapper>
                    {/* <span className={`sort-icon ${sortColumn === column.key ? sortDirection : 'NONE'}`}><DropDownIcon/></span> */}
                </SortableHeaderCell>
            );
        }

        return child ? child : column.name;
    }

    return <>{getCell()}</>;
}
