import { WithTranslation } from 'react-i18next';
import { CoinInfo, CoinKey } from 'static-resource';
import { Box, BoxProps, Grid } from '@material-ui/core';
import { Typography } from '@material-ui/core/';
import styled from '@emotion/styled';
import React from 'react';
import { floatTag, TradeFloat, useSettings } from './../../index';

type StyledProps = {
    custom: any
}
const MarketBlockStyled = styled(Box)<StyledProps>`

  & {
    background-color: ${({theme}) => theme.colorBase.background().default};
    ${({theme}) => theme.border.defaultFrame({c_key: 'blur', d_R: 1})};

  }

  // &:not(:last-of-type){
    //   margin-right: ${({theme}) => theme.unit * 3}px;
  //   background-color: chocolate;
  // }
  ${({theme, custom}) => floatTag({theme, custom})};

  .left-block {
    min-width: 76px;
    max-width: 84px;
  }

  .float-group span {
    display: flex;
    align-items: flex-end;
  }

` as React.ElementType<StyledProps & BoxProps>;

export const MarketBlock = <C extends CoinKey<I>, I>({buyData, t, sellData, tradeFloat}: {
    buyData: CoinInfo<C>,
    sellData: CoinInfo<C>,
} & WithTranslation & { tradeFloat: TradeFloat }) => {
    const {upColor} = useSettings();
    // const sellIconHasLoaded = useImage(sellData?.icon ? sellData?.icon : '').hasLoaded;
    // const buyIconHasLoaded = useImage(buyData?.icon ? buyData?.icon : '').hasLoaded;
    //TODO: get which currency is base
    //TODO: get which color increase
    return <MarketBlockStyled custom={{chg: upColor}} padding={0.5 * 5} display={'flex'}
                              justifyContent={'stretch'}>
        {buyData && sellData ?
            <Grid container justifyContent={'space-around'}>
                <Grid item xs={12} display={'flex'} flexDirection={'row'} justifyContent={'flex-start'}
                      alignItems={'center'} height={24}>
                    <Typography variant={'h3'} component={'h3'}>
                        <Typography component={'span'} title={'sell'} color={'textColorPrimary'}>
                            {sellData?.simpleName}
                        </Typography>
                        <Typography component={'i'}>/</Typography>
                        <Typography component={'span'} title={'buy'} color={'textColorPrimary'}>
                            {buyData.simpleName}
                        </Typography>
                    </Typography>
                </Grid>
                <Grid item xs={12} display={'flex'} flexDirection={'row'} justifyContent={'flex-start'}
                      alignItems={'stretch'} className={'float-group'} marginTop={1}>
                    <Box display={'flex'} flexDirection={'column'} alignItems={'flex-start'} justifyContent={'flex-end'}
                         className={'left-block'}>
                        <Typography variant={'h4'} height={24} component={'span'}
                                    className={`float-tag float-${tradeFloat.floatTag}`}> ${tradeFloat.priceDollar} </Typography>
                        <Typography variant={'body2'} component={'span'} marginTop={1 / 2}
                                    className={`float-tag float-${tradeFloat.floatTag}`}>{tradeFloat.percent}</Typography>
                    </Box>
                    <Box display={'flex'} flexDirection={'column'} alignItems={'flex-start'}
                         justifyContent={'flex-end'}>
                        <Typography variant={'body2'} component={'span'}
                                    height={24}> {tradeFloat.priceYuan} </Typography>
                        <Typography variant={'body2'} component={'span'}
                                    marginTop={1 / 2}>{t('labelVolume')} : {tradeFloat.volume}</Typography>
                    </Box>


                </Grid>

            </Grid> : <></>
        } </MarketBlockStyled>
}