import { SwapData, SwapProps, SwapTradeData } from '../Interface';
import { withTranslation, WithTranslation } from 'react-i18next';
import React from 'react';
import { Grid } from '@material-ui/core';
import { SwitchPanel, SwitchPanelProps } from '../../basic-lib';
import { DropDownIcon, IBData, RroToLiteIcon, TradeCalcData } from 'static-resource';
import { useDeepCompareEffect } from 'react-use';
import clockLoading from '../../../assets/svg/clock-loading.svg';
import { IconButtonStyled, SwapMenuList, SwapTradeWrap } from '../components';


export const SwapPanel = withTranslation('common', {withRef: true})(<T extends IBData<I>,
    I,
    TCD extends TradeCalcData<I>>({
                                      disabled,
                                      // doBeforeSwitch,
                                      // onCoinValueChange,
                                      handleSwapPanelEvent,
                                      tradeCalcData,
                                      handleError,
                                      onSwapClick,
                                      swapBtnStatus,
                                      tokenSellProps,
                                      tokenBuyProps,
                                      ...rest
                                  }: SwapProps<T, I, TCD> & WithTranslation) => {
    const swapTradeData = (rest.swapTradeData ? rest.swapTradeData : {sell: {}, buy: {}}) as SwapTradeData<T>;
    const [index, setIndex] = React.useState(0);
    const [swapData, setSwapData] = React.useState<SwapData<SwapTradeData<T>>>({
        to: 'button',
        type: 'buy',
        tradeData: {
            ...swapTradeData,
            sell: swapTradeData.sell.belong ? {
                belong: swapTradeData.sell.belong,
                balance: tradeCalcData.walletMap[ swapTradeData.sell.belong as any ]?.count
            } as T : {} as T,
            buy: swapTradeData.buy.belong ? {
                belong: swapTradeData.buy.belong,
                balance: tradeCalcData.walletMap[ swapTradeData.buy.belong as any ]?.count
            } as T : {} as T,

        }
    })

    useDeepCompareEffect(() => {
        if (swapTradeData !== swapData.tradeData) {
            setSwapData({...swapData, tradeData: swapTradeData});
        }
    }, [swapTradeData])
    const onChangeEvent = React.useCallback(async (index: 0 | 1, {to, tradeData, type}: SwapData<SwapTradeData<T>>) => {
        await handleSwapPanelEvent({to, tradeData, type}, type === 'exchange' ? 'exchange' : `${type}To${to}` as any);
        if (typeof rest.onChangeEvent == 'function') {
            setSwapData(rest.onChangeEvent(index, {to, tradeData, type}));
        } else {
            if (type === 'exchange') {
                const countBuy = tradeData.buy.belong ? tradeCalcData.walletMap[ tradeData.buy.belong ]?.count : 0;
                const countSell = tradeData.sell.belong ? tradeCalcData.walletMap[ tradeData.sell.belong ]?.count : 0;
                console.log('exchange')
                setSwapData({
                    tradeData: {
                        ...swapData.tradeData,
                        sell: {belong: tradeData.sell.belong, balance: countSell, tradeValue: 0},
                        buy: {belong: tradeData.buy.belong, balance: countBuy, tradeValue: 0},
                    } as SwapTradeData<T>, to, type: 'buy'
                });
            } else if (to === 'menu') {
                setSwapData({tradeData, to, type});
            } else if (to === 'button') {
                const count = tradeData[ type ].belong ? tradeCalcData.walletMap[ tradeData[ type ].belong ]?.count : 0;
                let _tradeData = {
                    ...tradeData,
                    [ type ]: {belong: tradeData[ type ].belong, balance: count ? count : 0, tradeValue: 0}
                };
                setSwapData({tradeData: _tradeData, to, type});
            }
        }
        setIndex(index);
    }, [handleSwapPanelEvent, tradeCalcData, rest]);
    const props: SwitchPanelProps<'tradeMenuList' | 'trade'> = {
        index: index, // show default show
        panelList: [
            {
                key: "trade",
                element: () => <SwapTradeWrap<T, I, TCD> key={"trade"} {...{
                    ...rest,
                    swapData,
                    tradeCalcData,
                    onSwapClick,
                    onChangeEvent,
                    disabled,
                    swapBtnStatus,
                    tokenSellProps,
                    tokenBuyProps,
                    handleError,
                }}/>,
                toolBarItem: () => <Grid container justifyContent={'flex-end'}>
                    <IconButtonStyled edge="end"
                                      className={'switch outline'}
                                      color="inherit"
                                      aria-label="to Professional">
                        <RroToLiteIcon/>
                    </IconButtonStyled>
                    <IconButtonStyled edge="end"
                                      className={'clock-loading outline'}
                                      color="inherit"
                                      aria-label="3' price update">
                        <img src={clockLoading} alt={'loading'} width={28} height={28}/>
                    </IconButtonStyled>
                </Grid>
            },
            {
                key: "tradeMenuList",
                element: () => <SwapMenuList<T, I, TCD> key={"tradeMenuList"} {...{
                    ...rest,
                    onChangeEvent,
                    tradeCalcData,
                    swapData
                }}/>,
                toolBarItem: () => <Grid container justifyContent={'flex-start'}>
                    <IconButtonStyled edge="start" sx={{transform: 'rotate(90deg)', transformOrigin: '50%'}}
                                      className={'switch'}
                                      color="inherit"
                                      onClick={() => onChangeEvent(0, swapData)}
                                      aria-label="to Professional">

                        <DropDownIcon/>
                    </IconButtonStyled>

                </Grid>
            },
        ],

    }
    return <SwitchPanel {...{...rest, ...props}} />
})

export const TransferMode = withTranslation()

