import { useDispatch, useSelector } from 'react-redux'
import { ModalState, ModalStatePlayLoad } from './interface';
import { setShowDeposit, setShowResetAccount, setShowTransfer, setShowWithdraw } from './reducer';
import { DepositInfoProps, ResetInfoProps, TransferInfoProps, WithdrawInfoProps } from '../../';
import { IBData } from 'static-resource';

export function useOpenModals() {
    const dispatch = useDispatch();
    return {
        modals: useSelector((state: any) => state.modals) as ModalState,
        setShowTransfer: (state: ModalStatePlayLoad & { props?: Partial<TransferInfoProps<IBData<any>, any>> }) => dispatch(setShowTransfer(state)),
        setShowDeposit: (state: ModalStatePlayLoad & { props?: DepositInfoProps<IBData<any>, any> }) => dispatch(setShowDeposit(state)),
        setShowWithdraw: (state: ModalStatePlayLoad & { props?: WithdrawInfoProps<IBData<any>, any> }) => dispatch(setShowWithdraw(state)),
        setShowResetAccount: (state: ModalStatePlayLoad & { props?: ResetInfoProps<IBData<any>, any> }) => dispatch(setShowResetAccount(state)),
    }

}
